* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  align-items: center;
  width: 100%;
  display: flex;
}

.header__box {
  flex-grow: 1;
  height: 60px;
  display: flex;
}

.header__box-logo {
  z-index: 1;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 10px;
  left: 50px;
}

.header__container {
  justify-content: flex-end;
  height: 60px;
  margin-left: 0;
  margin-right: 50px;
  display: flex;
}

.header__container-titles {
  align-items: center;
  list-style: none;
  display: flex;
}

.menu {
  color: #060b35;
  height: 60px;
  margin-left: 20px;
  line-height: 60px;
  transition: all .3s;
}

.menu:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  height: 60px;
  margin-left: 0;
  line-height: 60px;
  display: inline-block;
  position: relative;
}

.is-active:after {
  content: "";
  border-style: 10px solid;
  background-color: #00acdc;
  border-radius: 10px;
  width: 100%;
  height: 4px;
  transition: background-color .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card-container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .card-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .card-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .card-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border-radius: 5px;
  outline: 1px solid #f3f3f3;
  flex-direction: column;
  width: 200px;
  height: 408px;
  transition: all .3s;
  display: flex;
  transform: scale(1);
}

.card__img {
  width: 160px;
  height: 134px;
  margin-top: 33px;
  margin-bottom: 40px;
  margin-left: 20px;
}

.card__title {
  color: #060b35;
  align-items: center;
  width: 166px;
  height: 36px;
  margin-bottom: 4px;
  margin-left: 17px;
  line-height: 18px;
  display: flex;
  position: relative;
}

.card__code {
  color: #616070;
  align-items: center;
  width: 98px;
  height: 14px;
  margin-bottom: 16px;
  margin-left: 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
  position: relative;
}

.card__star {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin-bottom: 24px;
  margin-left: 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
  position: relative;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 18px;
  margin-bottom: 16px;
  margin-left: 17px;
  line-height: 18px;
  display: flex;
  position: relative;
}

.card__price-1 {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-2 {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__bottom {
  border-style: 1px Solid #00acdc;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-left: 17px;
  transition: all .3s;
  display: flex;
  position: relative;
}

.card__bottom-link {
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.card:hover {
  transform: scale(1.2);
}

.card__bottom:hover {
  border-style: 1px Solid #00acdc;
  background: #fff;
  box-shadow: inset 0 0 0 1px #00acdc;
}

.card__bottom:hover .card__bottom-link {
  color: #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  border-radius: .5px 0 0;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
